<template>
<div @scroll="handleScroll" id="topDiv" class="main-page" ref="mainpage">
    <Contact class="contact"/>
    <Snippet v-scrollanimation class="snippet"></Snippet>
    <transition name="fade">
    <ScrollDown v-if="show" v-scrolldisappear class="scroller"/>
    </transition>
    <Profile v-scrollanimation class="shadow profile"/>
    <Experience v-scrollanimation class="shadow experience"/>
</div>    
</template>

<script>
import Snippet from "../components/SnippetComponent";
import Profile from "../components/ProfileComponent";
import ScrollDown from "../components/ScrollDownComponent";
import Contact from "../components/ContactComponent";
import Experience from "../components/ExperienceComponent";
import Vue from 'vue'

export default {
  name: "Home",
  components: {Snippet , Profile, Contact, Experience, ScrollDown},
  data(){
    return{
      show: true,
    }
  },
  created () {
  },
  destroyed () {
  },
  methods: {
    handleScroll () {
      this.show = false;
      var element = document.getElementById("topDiv");
      if(element.scrollTop == 0){
        this.show = true;
      }
    },
  }
};
</script>

<style scoped>
@import '../css/media-snippet.css';
.main-page{
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  max-height:100%;
  overflow-y: auto;
  scroll-behavior: smooth;
}
::-webkit-scrollbar-track
{
	background-color: #17182f;
}
::-webkit-scrollbar
{
	width: 10px;
	background-color: #17182f;
}
::-webkit-scrollbar-thumb
{
	background-color: #1e1f3b;
	border: 2px solid #17182f;
}
.snippet{
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-80%, -50%);
  left:50%;
  position: fixed;
  top: 50%;
}
.profile{
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-80%, -50%);
  left:50%;
  position: fixed;
  top: 200%;
  width: 60%;
}
.before-enter{
  opacity:0;
  transition: all 2s cubic-bezier(.17,.67,.51,1.15);
}
.enter{
  opacity:1;
  transform: scale(1.0, 1.0);
  position: fixed;
  transform: translate(-50%, -50.1%);
}
.scroller{
  top: 75%;
  left: -1%;
  opacity: (0.5);
}
.before-leave{
  opacity:0;
  transform: translateY(-150%) !important;
  transition: all 2s ease-in-out;
}
.leave{
  opacity:1;
  transform: translateY(0%) !important;
}
.fade-leave-active {
  transition: opacity .5s;
}
.fade-leave-to{
  opacity: 0;
}
.contact{
  position: sticky;
  width:100%;
  top: 0% !important;
}
.experience{
  width: 80%;
  position: fixed;
  top: 370%;
  height: 70%;
  margin-bottom: 10% !important;
}
.shadow{
  box-shadow:
  0 2.8px 2.2px rgba(0, 0, 0, 0.034),
  0 6.7px 5.3px rgba(0, 0, 0, 0.048),
  0 12.5px 10px rgba(0, 0, 0, 0.06),
  0 22.3px 17.9px rgba(0, 0, 0, 0.072),
  0 41.8px 33.4px rgba(0, 0, 0, 0.086),
  0 100px 80px rgba(0, 0, 0, 0.12)
}
</style>
